













import uniListItem from './uni-list-item.vue';
import uniLoadMore from '../uni-load-more/uni-load-more.vue';

export default {
	components: {
		uniListItem,
		uniLoadMore
	},
	props: {
		dataSource: {
			type: Array,
			default () {
				return []
			}
		},
		loadingStatus: {
			type: Number,
			default: 0
		},
		enableScroll:{
			type:Boolean,
			default:true
		}
	},
	methods: {
		//
		navToDetails(item){
			let data = {
				id: item.id,
				title: item.title,
				author: item.author,
				time: item.time
			}
			let url = item.videoSrc ? 'videoDetails' : 'details'; 
		
			uni.navigateTo({
				url: `/pages/details/${url}?data=${JSON.stringify(data)}`
			})
		},
		loadMore() {
			this.$emit('loadMore');
		}
	}
}
